




import {Component, Vue, Provide} from 'vue-property-decorator';
import {GeoData} from '@/model/geo';
import {DataService} from '@/service/data.service';
import {ModelAttributes} from '@/interface/common';
import {ErrorHandlerService} from '@/service/error-handler.service';
import { HtmlParseDirective } from '@/directive/html-parse';
import VuePhoneNumberInput from 'vue-phone-number-input';

@Component({
  components: {
    'vue-phone-number-input': VuePhoneNumberInput,
  },
  directives: {
    'html-parse': HtmlParseDirective,
  },

})
export default class Form extends Vue {
  @Provide('validator') public $validator;
  public baseData: ModelAttributes | null = null;
  public baseGeo: GeoData | null = null;
  public loading = true;

  private dataService = new DataService();
  private errorService = new ErrorHandlerService();

  public mounted() {
    this.getData();
  }

  public errorHandler(errors, model) {
    this.errorService.send(errors, model);
  }

  public getData() {
    this.loading = true;

    this.dataService.getData()
      .subscribe(
        (response) => {
          this.loading = false;
          this.baseData = response;
        },
        () => null,
      );
  }



  public get iframe(): ModelAttributes {
    return this.baseData ? this.baseData.iframe : {};
  }


}
