import {Observable, ReplaySubject} from 'rxjs';
import axios from 'axios';
import {ModelAttributes} from '@/interface/common';

const dataApi = './data.json';
const zohoApi = 'zoho/lead';
const emailApi = 'https://crm.jokermg.com/Test_api/isEmailExist2?email=';

export class DataService {

  public getData(): Observable<ModelAttributes> {
    const result = new ReplaySubject<ModelAttributes>(1);

    axios
      .get(`${dataApi}`)
      .then((response) => result.next(response.data))
      .catch((err) => result.error(err));

    return result.asObservable();
  }

  public sendTo(rout, model): Observable<any> {
    const result = new ReplaySubject<ModelAttributes>(1);
    model = this.formatModelToFormData(model);
    axios
      .post(`${rout}`, model)
      .then((response) => {
        result.next(response);
      })
      .catch((err) => result.error(err));

    return result.asObservable();
  }

  public checkEmail(email): Observable<any> {
    const result = new ReplaySubject<ModelAttributes>(1);

    axios
      .post(`${emailApi}${email}` )
      .then((response) => {
        result.next(response);
      })
      .catch((err) => result.error(err));

    return result.asObservable();
  }

  public send(model): Observable<ModelAttributes> {
    const result = new ReplaySubject<ModelAttributes>(1);

    axios
      .post(`${zohoApi}`, this.formatModelToFormData(model))
      .then((response) => result.next(response.data))
      .catch((err) => result.error(err));

    return result.asObservable();
  }

  private formatModelToFormData(model): string {
    let result = '';

    for (const key of Object.keys(model)) {
      result += `${key}=${model[key]}&`;
    }

    return result.slice(0, result.length - 1);
  }
}
