import {Observable, ReplaySubject} from 'rxjs';

export abstract class BaseBrowserStorage {

  protected storage: Storage | any;
  private isSupported = false;

  constructor() {
    this.createStorage();

    if (this.canBeUsed()) {
      this.isSupported = true;
    }
  }

  /**
   *
   * @param {string} key
   * @returns {Observable<string>}
   */
  public find(key: string): Observable<string> {
    const result = new ReplaySubject<string>(1);
    const data = this.storage.getItem(key);

    result.next(data);

    return result.asObservable();
  }

  /**
   *
   * @param {string} key
   * @param {string} value
   * @returns {Observable<boolean>}
   */
  public create(key: string, value: string): Observable<boolean> {
    const result = new ReplaySubject<boolean>(1);

    try {
      this.storage.setItem(key, value);
      result.next(true);
    } catch (e) {
      result.error(e);
    }

    return result.asObservable();
  }

  /**
   *
   * @param {string} key
   * @returns {Observable<boolean>}
   */
  public remove(key: string): Observable<boolean> {
    const result = new ReplaySubject<boolean>(1);

    try {
      this.storage.removeItem(key);
      result.next(true);
    } catch (e) {
      result.error(e);
    }

    return result.asObservable();
  }

  protected abstract createStorage(): void;

  /**
   *
   * @returns {boolean}
   */
  private canBeUsed(): boolean {
    let result = true;

    try {
      const x = 'test';

      this.storage.setItem(x, x);
      this.storage.removeItem(x);
    } catch (e) {
      result = false;
    }

    return result;
  }
}
