import {ModelAttributes} from '@/interface/common';
import {SessionStorageService} from '@/service/session-storage.service';
// import {SentryService} from '@/service/sentry.service';

const FORM_ERROR_KEY = 'fr-validation-error';

export class ErrorHandlerService {

  private sessionStorage = new SessionStorageService();
  // private sentry = new SentryService();

  public send(errors, model: ModelAttributes) {
    this.sessionStorage.find(FORM_ERROR_KEY)
      .subscribe(
        (response: string) => {
          if (!response) {
            const temp = errors.filter((error) => error.field === 'email' || error.field === 'phone' );
            if (temp.length < 2) {
              this.sessionStorage.create(FORM_ERROR_KEY, JSON.stringify(model));
              // this.sentry.sendValidationException(errors, model);
            }
          }
        },
      );
  }
}
