import { DirectiveOptions } from 'vue';
import sanitizeHtml from 'sanitize-html';

const allowedTags: string[] = ['strong', 'span', 'br', 'p', 'a', 'b'];

export const HtmlParseDirective: DirectiveOptions = {
  inserted(el, node) {
    const value = node.value;

    if (value) {
      el.innerHTML = sanitizeHtml(value, {allowedTags});
    }
  },
};
